.header {
  width: 100%;
  margin: 0 auto;
  background-color: rgba(21, 10, 1, 0.21);
  border-bottom: 1px solid rgba(255, 255, 255, 0.21);
  box-sizing: border-box;
  mix-blend-mode: normal;
}

.header__content {
  max-width: 1170px;
  margin: 0 auto;
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

