.header__corners {
  width: 316px;
  height: 100px;
  background-image: url('../../../public/images/corners.png');
  background-repeat: no-repeat;
  background-size: contain;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__logo {
  /* background-image: url('../../img/lg1.png'); */
  /* background-image: url('../../../public/images/logo.png'); */
  background-image: url('../../img/logo55.png');
  background-repeat: no-repeat;
  background-size: contain;
  width: 274px;
  height: 34px;
  margin: 0 0 10px 5px;
}

.header__nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 40px;
}

.header__nav-item {
  font-family: 'Museo';
  color: #fff;
  font-weight: 300;
  font-size: 16px;
  line-height: 22px;
  text-decoration: none;
}

.header__nav-item:hover {
  cursor: url('../../../public/images/cursor.png'), pointer;
  /* cursor: url('../../img/logo55.png'), pointer; */
  color: #3d3d3d;
  border-bottom: 1px solid #3d3d3d;
}
